<template>
  <div class="bussiness-records">
    <div class="topTitle">
      <span class="topTitle-span1">洗钱风险评估</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">机构洗钱风险评估</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">洗钱风险评估记录</span>
      <div class="button-content">
        <!-- <Button type="primary" @click="operate('add')">
          <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
        </Button>
        <Button type="primary">显示</Button>
        <Button type="primary">排序</Button>-->
      </div>
    </div>
    <div class="content2">
      <Form
        ref="formValidate"
        :model="formValidate"
        :label-width="120"
        inline
      >
        <Row>
          <FormItem label="管理部门:">
            <treeselect
              v-model="formValidate.val1"
              valueConsistsOf="ALL" 
              :limit="3"
              :multiple="true"
              :options="data1"
              placeholder="请选择"
              :max-height="100"
              style="width:200px"
            />
          </FormItem>
          <!-- <FormItem label="业务名称:" prop>
            <Input v-model="formValidate.formname" placeholder="请输入" style="width:200px"></Input>
          </FormItem> -->
          <FormItem label="评估日期:">
            <DatePicker
              :value="formValidate.val2"
              format="yyyy-MM-dd"
              type="daterange"
              placement="bottom-end"
              placeholder="请选择时间"
              style="width: 200px"
              @on-change="formValidate.val2=$event"
            ></DatePicker>
          </FormItem>
          <FormItem label="评估期间:">
            <DatePicker
              :value="formValidate.val3"
              format="yyyy-MM-dd"
              type="daterange"
              placement="bottom-end"
              placeholder="请选择时间"
              style="width: 200px"
              @on-change="formValidate.val3=$event"
            ></DatePicker>
          </FormItem>
            <FormItem label="审核状态:" prop>
            <Select v-model="formValidate.val4" placeholder="请选择业务" style="width:200px">
              <Option :value="''">未选择</Option>
              <Option v-for="item in EVAL_STA" :value="item.code" :key="item.code">{{item.name}}</Option>n
            </Select>
          </FormItem>
            <FormItem label="固有等级:" prop>
            <Select v-model="formValidate.val5" placeholder="请选择业务" style="width:200px">
              <Option :value="''">未选择</Option>
              <Option v-for="item in getRiskLevel" :value="item.levelCode" :key="item.levelCode">{{item.levelName}}</Option>n
            </Select>
          </FormItem>
            <FormItem label="措施等级:" prop>
            <Select v-model="formValidate.val6" placeholder="请选择业务" style="width:200px">
              <Option :value="''">未选择</Option>
              <Option v-for="item in orgContrRiskLevel" :value="item.levelCode" :key="item.levelCode">{{item.levelName}}</Option>n
            </Select>
          </FormItem>
              <FormItem label="评估等级:" prop>
            <Select v-model="formValidate.val7" placeholder="请选择业务" style="width:200px">
              <Option :value="''">未选择</Option>
              <Option v-for="item in orgCompLevelVo" :value="item.levelCode" :key="item.levelCode">{{item.levelName}}</Option>n
            </Select>
          </FormItem>
          <!-- ================ -->
        </Row>
        <Row>
          <div class="btnClass">
            <Button type="primary" @click="handleSubmit('formValidate')" style="width:89px">查询</Button>
          </div>
        </Row>
      </Form>
    </div>
    <div class="contentCellBody">
      <Row>
        <div class="content1">
          <div class="contentCellTitle">
            <div class="contentCellTitle-1"></div>数据列表
          </div>
        </div>
      </Row>
      <Row>
        <table class="tableBox">
          <thead>
            <tr>
              <td v-for="(item,index) in tableHeader">{{item.name}}</td>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item,index) in tableBody">
              <td>{{pageSize*(pageNum-1)+index+1}}</td>
              <td>
                <span
                  class="spanBox"
                  @click="viewDetail(item)"
                >{{item.orgName}}</span>
              </td>
              <!-- 管理部门 -->
              <td>{{item.evalDate}}</td>
              <!-- 评估日期 -->
              <td>{{item.evalPeriod}}</td>
              <!-- 评估期间 -->
              <td>{{item.status}}</td>
              <!-- 审核状态 -->
              <td>{{item.evalScore}}</td>
              <!-- 固有风险评估（分 -->
              <td>{{item.evalLevel}}</td>
              <!-- 固有风险等级 -->
              <td>{{item.contrScore}}</td>
              <!-- 控制措施有效性评估（分） -->
              <td>{{item.contrLevel}}</td>
              <!-- 控制措施有效性评估等级 -->
              <td>{{item.compLevel}}</td>
              <!-- 综合评估 -->
               <td>{{item.compName}}</td>
              <!-- 综合等级 -->
              <td>
                <!-- {{
                item.remark
                }} -->
                  <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="item.remark"
                  >
                    <p slot="reference" class="textOverflow textMaxWidth">{{item.remark}}</p>
                  </el-popover>
                </td>
              <!--  评估备注-->
              <td  >
                <span   class="spanBox"
                @click="watchFiles(item.businessId)">查看</span>
              </td>
            </tr>
          </tbody>
        </table>
      </Row>
    </div>
    <div class="flexCenter pageBox">
      <Page
        :total="rowCount"
        :current.sync="pageNum"
        :page-size="pageSize"
        @on-change="pageChange"
        @on-page-size-change="pageSizefun"
        show-elevator
        show-sizer
      />
    </div>
    <Modal :mask-closable="false"   v-model="modal1" title="文件列表">
      <div class="fujian">
        <div class="fujian-title">附件:</div>
        <div v-for="(item,index) in fileList" :key="index">
          <span
            style="width:100%;height:40px;line-height:20px;overflow:hidden;text-overflow: ellipsis;margin-bottom:5px"
            class="cursorPointer"
            @click="fileDownload(item.fileId, item.filename)"
          >{{item.filename}}</span>
        </div>
      </div>
      <div slot="footer"></div>
    </Modal>
  </div>
</template>
<script>
import assessDetail from "../common/assessDetail"; //
import Treeselect from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";
export default {
  components: { Treeselect, assessDetail },
  created() {
      let that = this;
      this.tableList();
      this.axios
        .post("/system/unit/getExtractUnit")
        .then(response => {
          if (response.code == "200") {
            that.selectTreeCleanUp(response.data);
            that.data1 = response.data;
          }
        })
        .catch(error => {});
        this.axios
        .get("/system/getDic/EVAL_STA", {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            this.EVAL_STA=response.data.EVAL_STA;
          }
        })
        .catch(error => {});
        this.axios
        .get("/model/orgRisklevel/getRiskLevel", {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            this.getRiskLevel=response.data.orgRiskLevel;
            this.orgContrRiskLevel=response.data.orgContrRiskLevel;
          this.orgCompLevelVo=response.data.orgCompLevelVo
          }
        })
        .catch(error => {});
  },
  data() {
    return {
      getRiskLevel:[],
      EVAL_STA:[],
      orgContrRiskLevel:[],
      orgCompLevelVo:[],
      pageSize: 10,
      rowCount: 0,
      pageNum: 1,
      modal1: false,
      fileList: [],
      data1: [],
      tableHeader: [
        { name: "序" },
        { name: "管理部门" },
        { name: "评估日期" },
        { name: "评估期间" },
        { name: "审核状态" },
        { name: "固有风险评估（分）" },
        { name: "固有风险等级" },
        { name: "控制措施有效性评估（分）" },
        { name: "控制措施有效性评估等级" },
        { name: "综合评估" },
        { name: "评估等级" },
        { name: "评估备注" },
        { name: "附件" },
      ],
      tableBody: [],
      formValidate: {
          val1:[],
          val2:['',''],
          val3:['',''],
          val4:'',
          val5:'',
          val6:'',
          val7:'',
      },
    };
  },
  methods: {
    tableList(){
      let that = this;
      let data = {
        department: this.formValidate.val1,
        evalDate: this.formValidate.val2[0],
        evalDate1: this.formValidate.val2[1],
        evalPeriod: this.formValidate.val3[0],
        evalPeriod1: this.formValidate.val3[1],
        status: this.formValidate.val4,
        evalLevel: this.formValidate.val5,
        contrLevel: this.formValidate.val6,
        compLevel: this.formValidate.val7,
        pageNum: this.pageNum,
        pageSize: this.pageSize,
      };
      data = JSON.stringify(data);
 
      this.axios
        .post("/risk/org/getOrgInfoRecord", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
           
            that.tableBody = response.data.records;
            that.rowCount = response.data.rowCount;
            
          }
        })
        .catch(error => {});
    },
    viewDetail(item){
      const { href } = this.$router.resolve({
          path: "/launderOrgainzationPage",
          query: {id:item.id,businessId:item.businessId,newId:item.newId}
      });
      window.open(href, '_blank');
    },
    watchFiles(id) {
      this.fileList = [];
      if(id){
        this.axios
        .post(`/system/remote/getFileList/${id}`)
        .then(res => {
          if (res.code == "200") {
            this.fileList = res.data;
          }
        })
        .catch(function(error) {});
      }
      this.modal1 = true;
      
    },
    fileDownload(filePath, fileName) {
      let that = this;
      let data = JSON.stringify({
        token: "",
        fileId: filePath
      });
      this.axios
        .post("/system/remote/micDownLoad", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          let data2 = JSON.stringify({
            url: response.message,
            fileName: fileName
          });
          this.axios
            .post(`/system/remote/getFileInfo`, data2, {
              headers: { "Content-Type": "application/json" },
              responseType: "blob"
            })
            .then(res => {
              this.download(res, fileName);
            })
            .catch(function(error) {});
        })
        .catch(error => {});
    },
    download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    selectTreeCleanUp(treeDatas) {
      for (let i = 0; i < treeDatas.length; i++) {
        if (treeDatas[i].children && treeDatas[i].children.length > 0) {
          this.selectTreeCleanUp(treeDatas[i].children);
        } else this.$delete(treeDatas[i], "children");
      }
    },
    pageSizefun(a) {
      this.pageSize = a;
      this.tableList();
    },
    handleSubmit(name, exactquery) {
        this.pageNum=1;
        this.tableList();
    },
    pageChange(pageNo) {
      let that = this;
      this.pageNum = pageNo;
      this.tableList();
    }
  }
};
</script>
<style lang="scss" scoped>
.ivu-tag {
  background-color: #fa9324;
}
.textOverBox {
  max-width: 150px !important;
  height: 40px;
  white-space: nowrap;
  line-height: 40px;
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: pointer;
}
.bussiness-records {
  background: #fff;
  .content2 {
    padding: 20px 0;
    // border-top: 1px solid #eef3f8;
    .content1 {
      margin-left: 35px;
      .contentCellTitle {
        display: inline-block;
        width: 100px;
        font-size: 16px;
        vertical-align: top;
        .contentCellTitle-1 {
          width: 3px;
          height: 18px;
        }
      }
      .header-wrapper {
        width: 80%;
        display: inline-block;
        padding: 5px 0;
        .ivu-tag {
          padding: 0px 5px;
          margin: 11px 4px 4px 0px;
          height: 28px;
          line-height: 28px;
          background: #fa9324;
          &.ivu-tag-checked {
            span {
              &.ivu-tag-text {
                color: #fff;
              }
            }
          }
          span {
            &.ivu-tag-text {
              color: #fff;
            }
          }
          &:not(.ivu-tag-border):not(.ivu-tag-dot):not(.ivu-tag-checked) {
            background: 0 0;
            span {
              &.ivu-tag-text {
                color: #b6b6b6;
              }
            }
          }
        }
        span {
          font-size: 14px;
          margin: 0 10px;
          cursor: pointer;
          &.active {
            background: #fa9324;
            border-radius: 2px;
            color: #fff;
          }
        }
      }
    }
  }
  .contentCellBody {
    padding: 0 10px;
    .tableBox {
      thead {
        td {
          width: 100px;
        }
      }
    }
  }
  .btnClass {
    text-align: center;
  }
}
</style>



